
html, body, #app {
  width: 100%; height: 100%; margin: 0px; padding: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#canvas-container {
  background: black;
  height: calc(100% - 0px);
  width: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
#canvas {
  width: 100%;
  height: 100%;
}
#top {
  position: absolute;
  top: 0px;
  left: 0px;
  background: rgba(0,0,0,0.25);
  color: white;
  width: 100%;
  height: 50px;
  line-height: 50px;
}
#menu {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.25);
  color: white;
}
#menu-header {
  height: 50px;
  line-height: 50px;
  background: rgba(0,0,0,0.25);
  padding-left: 20px;
  font-weight: bold;
}
.menu-items {
  padding: 10px;
}
.menu-item {
  margin-bottom: 10px;
  background: rgba(0,0,0,0.125);
  padding: 10px;
}
.menu-item-selected {
  font-weight: bold;
  background: rgba(0,0,0,0.25);
}
.top-item, .top-item-right {
  height: 50px;
}
.top-item > i, .top-item-right > i {
  font-size: 28px;
  line-height: 50px;
}
.top-item {
  margin-left: 10px;
  float: left;
}
.top-item-right {
  float: right;
  margin-right: 10px;
}
